html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%;
  font-family: "Montserrat", sans-serif; }

body {
  background: #f7f7fe;
  font-weight: 700; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 700; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1090px;
  width: 100%;
  padding: 0 20px 0;
  margin: 0 auto; }

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.pricing {
  padding-top: 71px;
  padding-bottom: 103px;
  color: #6e728e; }
  @media screen and (max-width: 650px) {
    .pricing {
      padding-top: 64px; } }

.pricing-title {
  text-align: center;
  font-size: 32px;
  margin-bottom: 40px; }

.pricing-switcher {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 64px; }
  @media screen and (max-width: 650px) {
    .pricing-switcher {
      margin-bottom: 80px; } }
  .pricing-switcher__value {
    font-size: 15px; }
  .pricing-switcher__item {
    margin: 0 24px; }
  .pricing-switcher__input {
    width: 56px;
    height: 32px;
    border: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    background: linear-gradient(135deg, #a2a7f0 0%, #696edd 100%);
    border-radius: 16px;
    position: relative;
    transition: 0.3s all; }
    .pricing-switcher__input::after {
      content: "";
      background: #ffffff;
      border-radius: 50%;
      width: 24px;
      height: 24px;
      position: absolute;
      left: 5px;
      top: 50%;
      transform: translateY(-50%);
      transition: 0.3s all; }
    .pricing-switcher__input:hover {
      opacity: 0.5; }
    .pricing-switcher__input:checked::after {
      left: 27px; }

.pricing-content {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media screen and (max-width: 650px) {
    .pricing-content {
      flex-direction: column; } }

.pricing-card {
  max-width: 350px;
  width: 100%;
  padding: 31px;
  text-align: center;
  border-radius: 10px 0px 0px 10px; }
  @media screen and (max-width: 650px) {
    .pricing-card {
      max-width: 327px;
      padding: 31px 29px;
      margin-bottom: 63px; } }
  .pricing-card__title {
    font-size: 18px;
    margin-bottom: 24px; }
  .pricing-card__price {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px; }
    .pricing-card__price span:first-child {
      font-size: 40px;
      margin-right: 8px; }
  .pricing-card__value {
    font-size: 72px;
    letter-spacing: -2.16px; }
  .pricing-card__list {
    margin-bottom: 32px; }
  .pricing-card__item {
    font-size: 15px;
    padding: 12px 0;
    border-top: 1px solid rgba(110, 114, 142, 0.25); }
    .pricing-card__item:last-child {
      border-bottom: 1px solid rgba(110, 114, 142, 0.25); }
  .pricing-card__button {
    width: 100%;
    height: 44px;
    background: #696edd;
    border-radius: 6px;
    font-size: 13px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s all; }

.pricing-card:nth-child(2) {
  padding: 55px 31px;
  height: auto;
  background: linear-gradient(135deg, #a2a7f0 0.05%, #696edd 100.05%);
  box-shadow: 0px 20px 40px rgba(212, 210, 244, 0.5);
  border-radius: 10px; }
  @media screen and (max-width: 650px) {
    .pricing-card:nth-child(2) {
      padding: 31px 29px; } }
  .pricing-card:nth-child(2) .pricing-card__title,
  .pricing-card:nth-child(2) .pricing-card__item {
    color: #ffffff; }
  .pricing-card:nth-child(2) .pricing-card__price {
    color: #ffffff; }
  .pricing-card:nth-child(2) .pricing-card__button {
    background: #fff;
    color: #6d72de; }
    .pricing-card:nth-child(2) .pricing-card__button:hover {
      color: #ffffff;
      background-color: transparent;
      border: 1px solid #ffffff; }
  .pricing-card:nth-child(2) .pricing-card__item {
    border-top: 1px solid rgba(255, 255, 255, 0.25); }
    .pricing-card:nth-child(2) .pricing-card__item:last-child {
      border-bottom: 1px solid rgba(255, 255, 255, 0.25); }

.pricing-card:last-child, .pricing-card:first-child {
  height: auto;
  background: #ffffff; }
  .pricing-card:last-child .pricing-card__title,
  .pricing-card:last-child .pricing-card__item, .pricing-card:first-child .pricing-card__title,
  .pricing-card:first-child .pricing-card__item {
    color: #6e728e; }
  .pricing-card:last-child .pricing-card__price, .pricing-card:first-child .pricing-card__price {
    color: #4a4d60; }
  .pricing-card:last-child .pricing-card__button, .pricing-card:first-child .pricing-card__button {
    color: #ffffff; }
    .pricing-card:last-child .pricing-card__button:hover, .pricing-card:first-child .pricing-card__button:hover {
      background: #ffffff;
      border: 1px solid #6d72de;
      color: #6d72de; }

.pricing-card:first-child {
  box-shadow: 0px 20px 40px rgba(212, 210, 244, 0.5);
  border-radius: 10px 0px 0px 10px; }

.pricing-card:last-child {
  box-shadow: 0px 20px 40px rgba(212, 210, 244, 0.5);
  border-radius: 0px 10px 10px 0px; }
